/* ÌöåÏõê Î™®Îìú ÌïÑÌÑ∞ Ïª®ÌÖåÏù¥ÎÑà */
.member-mode-filters {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    gap: 30px;
    margin-top: 20px;
    margin-bottom: 30px;
    padding: 15px 50px;
    border-radius: 8px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.5);
    flex-wrap: wrap;
    overflow: hidden;
    position: relative; /* ÏûêÏãù ÏöîÏÜå z-index Í∏∞Ï§Ä */

    /* ÎÑ∑ÌîåÎ¶≠Ïä§ ÏãúÏûë ÌôîÎ©¥Í≥º Ïú†ÏÇ¨Ìïú Î∞∞Í≤Ω Í∑∏ÎùºÎç∞Ïù¥ÏÖò */
    background: radial-gradient(
            ellipse at center, /* Ï§ëÏïôÏóêÏÑú ÌçºÏßÄÎäî ÌÉÄÏõêÌòï Í∑∏ÎùºÎç∞Ïù¥ÏÖò */
            rgba(190, 30, 30, 0.99) 0%, /* Ï§ëÏïôÏùÄ ÎÑ∑ÌîåÎ¶≠Ïä§ Îπ®Í∞ÑÏÉâÏùò ÏòÖÏùÄ Î≤ÑÏ†Ñ */
            rgba(108, 23, 25, 0.91) 25%, /* Ï°∞Í∏à Îçî ÌçºÏßÄÎ©¥ÏÑú ÏòÖÏñ¥Ïßê */
            rgba(20, 20, 20, 1) 75%, /* Î∞îÍπ•Ï™ΩÏùÄ Í±∞Ïùò Í≤ÄÏùÄÏÉâ */
            #000000 100% /* Í∞ÄÏû• Î∞îÍπ•Ï™ΩÏùÄ ÏôÑÏ†Ñ Í≤ÄÏùÄÏÉâ */
    );
    background-size: 150% 150%; /* Í∑∏ÎùºÎç∞Ïù¥ÏÖò ÌÅ¨Í∏∞Î•º ÌÇ§Ïõå ÎØ∏Î¨òÌïòÍ≤å Î≥¥Ïù¥ÎèÑÎ°ù */
    background-position: center; /* Ï§ëÏïô Ï†ïÎ†¨ */
    animation: netflixIntroBackground 10s ease-in-out infinite alternate; /* ÏùÄÏùÄÌïòÍ≤å ÏõÄÏßÅÏù¥Îäî Ïï†ÎãàÎ©îÏù¥ÏÖò */

    /* Í∏∞Ï°¥Ïóê ÏûàÎçò Ï§ÑÎ¨¥Îä¨ Î∞∞Í≤Ω Í¥ÄÎ†® Ïä§ÌÉÄÏùº Ï†úÍ±∞ */
    /* background: linear-gradient( ... ); */
    /* background-size: 200px 100%; */
    /* background-position: 0% 0%; */
    /* animation: slideBackground 20s linear infinite; */
}

/* ÎÑ∑ÌîåÎ¶≠Ïä§ ÏãúÏûë ÌôîÎ©¥ Î∞∞Í≤Ω ÏõÄÏßÅÏûÑ Ïï†ÎãàÎ©îÏù¥ÏÖò ÌÇ§ÌîÑÎ†àÏûÑ */
@keyframes netflixIntroBackground {
    0% { background-position: 0% 0%; }
    50% { background-position: 100% 100%; } /* ÎåÄÍ∞ÅÏÑ†ÏúºÎ°ú ÏõÄÏßÅÏòÄÎã§ ÎèåÏïÑÏò§ÎèÑÎ°ù */
    100% { background-position: 0% 0%; }
}


/* ... (ÎÇòÎ®∏ÏßÄ filter-group, label, button-group, genre-item Ïä§ÌÉÄÏùº Ïú†ÏßÄ) ... */



/* Î∞∞Í≤Ω ÏõÄÏßÅÏûÑ Ïï†ÎãàÎ©îÏù¥ÏÖò ÌÇ§ÌîÑÎ†àÏûÑ */
@keyframes slideBackground {
    from { background-position: 0% 0%; }
    to { background-position: 100% 0%; } /* Î∞∞Í≤ΩÏùÑ Ïò§Î•∏Ï™ΩÏúºÎ°ú Ïù¥Îèô */
}


/* ... (ÎÇòÎ®∏ÏßÄ filter-group, label, button-group, genre-item Ïä§ÌÉÄÏùº Ïú†ÏßÄ) ... */

/* genre-item ÎÇ¥Î∂Ä ÌÖçÏä§Ìä∏ÏôÄ ÏïÑÏù¥ÏΩòÏùò z-indexÎ•º ÎÜíÏó¨ Î∞∞Í≤Ω ÏúÑÏóê Î≥¥Ïù¥ÎèÑÎ°ù */
.filter-group label, .button-group .genre-item {
    position: relative;
    z-index: 2; /* Î∞∞Í≤Ω ÏúÑÏóê Ïò§ÎèÑÎ°ù */
    /* ÌïÑÏöîÌïòÎã§Î©¥ text-shadowÎ•º Îçî Í∞ïÌïòÍ≤å Ï§Ñ Ïàò ÏûàÏäµÎãàÎã§. */
    text-shadow: 0 0 5px rgba(0,0,0,0.8);
}

/* genre-item ÎÇ¥Î∂Ä ÌÖçÏä§Ìä∏ÏôÄ ÏïÑÏù¥ÏΩòÏùò z-indexÎ•º ÎÜíÏó¨ Î∞∞Í≤Ω ÏúÑÏóê Î≥¥Ïù¥ÎèÑÎ°ù */
.filter-group label, .button-group .genre-item {
    position: relative;
    z-index: 2; /* Î∞∞Í≤Ω ÏúÑÏóê Ïò§ÎèÑÎ°ù */
    text-shadow: 0 0 5px rgba(0,0,0,0.8); /* ÌÖçÏä§Ìä∏ Í∑∏Î¶ºÏûê */
}