
.nav2 {
    position: fixed;
    top: 0;
    width: 100%;
    height: 30px;
    z-index: 1000;
    padding: 20px 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    transition-timing-function: ease-in;
    transition: all 0.5s;
    background-color: transparent;
}

.nav2__black {
    background-color: #111;
}

.nav__left {
    display: flex;
    align-items: center;
}

.nav__logo {
    width: 200px;
    object-fit: contain;
    cursor: pointer;
    margin-right: 20px;
}

.nav__recommend {
    color: #fff;
    font-size: 20px;
    cursor: pointer;
    margin-right: 5px; /* Ï∂îÏ≤úÍ≥º ÌîÑÎ°úÌïÑ Î°úÍ≥† Í∞ÑÍ≤© */
}

