    gap: 10px; /* üí° ÏöîÏÜå Í∞Ñ Ïó¨Î∞±ÏùÄ gapÏúºÎ°ú Ï°∞Ï†à */
    position: relative;
    overflow: visible; /* ÌòπÏãúÎùºÎèÑ Í∞ÄÎ†§ÏßÄÎäî Í±∞ Î∞©ÏßÄ */
    padding-right: 0; /* Ï∂îÍ∞Ä */
}

