/* src/styles/MainContent.css */
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700;900&display=swap');
/* .main-content Ïª¥Ìè¨ÎÑåÌä∏Ïùò ÏµúÏÉÅÏúÑ Ïª®ÌÖåÏù¥ÎÑà */
.main-content {
    position: relative;
    width: 100%;
    padding-top: 0;
    min-height: calc(100vh - 0px);
    background-color: #000000;
    padding-bottom: 20px;
}
/* --- ÎÑ∑ÌîåÎ¶≠Ïä§ Î©îÏù∏ ÏÉÅÎã® ÌîºÏ≤òÎìú ÏΩòÌÖêÏ∏† ÏÑπÏÖò (`image_87b43f.jpg`Ï≤òÎüº) --- */


/* Featured contentÏùò Í∑∏ÎùºÎç∞Ïù¥ÏÖò Ïò§Î≤ÑÎ†àÏù¥ (Ïù¥ÎØ∏ÏßÄ ÏúÑÏóê ÎçÆÏñ¥ÏîåÏõÄ) */
.featured-content {
    position: relative;
    height: 700px;
    background-size: cover;
    background-position: center;
    display: flex;
    align-items: center;
    padding: 0 50px;
    color: white;
    text-shadow: 5px 5px 5px rgba(0,0,0,0.5);
    background-color: #141414;
    /* Ïä¨Î°úÏö∞ Ï†ÑÌôò Ìö®Í≥º Ï∂îÍ∞Ä */
    transition: background-image 1s ease-in-out; /* Î∞∞Í≤Ω Ïù¥ÎØ∏ÏßÄ Ï†ÑÌôò Ïãú Î∂ÄÎìúÎüΩÍ≤å */

}
.featured-content::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to bottom, rgba(0,0,0,0.4) 0%, rgba(0,0,0,0) 60%, rgba(0,0,0,1) 100%);
    z-index: 1;
    opacity: 20;
}

.featured-info {
    max-width: 40%; /* ÌÖçÏä§Ìä∏ ÏòÅÏó≠ ÎÑàÎπÑ (Ï°∞Ï†ï Í∞ÄÎä•) */
    text-align: left;
    padding-bottom: 0px; /* ÌïòÎã® Ïó¨Î∞± */
    z-index: 2; /* ÌÖçÏä§Ìä∏Í∞Ä Ïò§Î≤ÑÎ†àÏù¥ ÏúÑÏóê Ïò§ÎèÑÎ°ù */
}

.featured-info h1 {
    font-family: 'Oswald', sans-serif; /* Featured Ï†úÎ™©ÏóêÎßå Roboto Ï†ÅÏö© */
    font-size: 3.0em;
    margin-bottom: 15px;
    color: #e0d3d3; /* ÏïΩÍ∞Ñ ÌöåÏÉâÎπõ ÎèÑÎäî Ìù∞ÏÉâ */
    text-shadow: 8px 5px 5px rgba(5, 5, 5, 0.7);
    font-weight: 900; /* Roboto Bold */
}

.featured-info p {
    font-size: 1.0em;
    line-height: 1.5;
    margin-bottom: 15px;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-shadow: 3px 4px 5px rgba(2, 2, 2, 0.7);
    text-overflow: ellipsis;
    color: #dad6d6; /* Îçî Î∞ùÏùÄ ÌöåÏÉâ */
    font-family: 'Roboto', sans-serif; /* Featured ÏÑ§Î™ÖÏóêÎßå Roboto Ï†ÅÏö© */
    font-weight: 400; /* Roboto Regular */
}

.featured-buttons {
    display: flex;
    gap: 15px;
}

.featured-buttons .play-button,
.featured-buttons .info-button {
    background-color: rgba(255, 255, 255, 0.2);
    color: white;
    border: none;
    padding: 12px 30px; /* Î≤ÑÌäº Ìå®Îî© ÌÇ§ÏõÄ */
    border-radius: 5px;
    font-size: 1.2em; /* Î≤ÑÌäº Í∏ÄÏî® ÌÇ§ÏõÄ */
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: center;
}

.featured-buttons .play-button:hover,
.featured-buttons .info-button:hover {
    background-color: rgba(255, 255, 255, 0.4);
}
.featured-buttons i {
    margin-right: 10px;
}

/* Ïä¨ÎùºÏù¥Îìú ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Î≤ÑÌäº */
.featured-nav-button {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    background: rgba(0, 0, 0, 0.5);
    color: white;
    border: none;
    font-size: 3rem;
    padding: 10px 15px;
    cursor: pointer;
    z-index: 3;
    opacity: 0; /* ÌèâÏÜåÏóêÎäî Ïà®ÍπÄ */
    transition: opacity 0.5s ease-in-out;
}

.featured-content:hover .featured-nav-button {
    opacity: 0.7; /* Ìò∏Î≤Ñ Ïãú ÏïΩÍ∞Ñ Î∂àÌà¨Î™ÖÌïòÍ≤å */
}

.featured-nav-button-left {
    left: 20px;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.featured-nav-button-right {
    right: 20px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}


/* --- Ï∂îÏ≤ú Ïπ¥ÌÖåÍ≥†Î¶¨ Î∞î (Î©îÏù∏ ÌîºÏ≥êÎìú ÏΩòÌÖêÏ∏† ÏïÑÎûòÎ°ú Ïû¨Î∞∞Ïπò) --- */
.recommendation-categories {
    display: flex;
    align-items: center;
    justify-content: center; /* Ï§ëÏïô Ï†ïÎ†¨ */
    position: relative;
    z-index: 10;
    margin-top: -90px; /* Featured Content ÏúÑÎ°ú Í≤πÏπòÍ≤å (ÏùåÏàò ÎßàÏßÑ) */
    padding: 0 50px; /* Featured ContentÏôÄ ÎèôÏùºÌïú Ï¢åÏö∞ Ìå®Îî© */
    margin-bottom: 0px; /* Ïπ¥ÌÖåÍ≥†Î¶¨ Î∞îÏôÄ Îã§Ïùå ÏÑπÏÖò Í∞ÑÍ≤© (5px) */
    /*background-color: #00000; !* Î©îÏù∏ Î∞∞Í≤ΩÏÉâÏùÑ ÏÉÅÏÜçÎ∞õÎèÑÎ°ù Ìà¨Î™ÖÌïòÍ≤å ÏÑ§Ï†ï *!*/
}

.category-nav-button {
    background: rgba(25, 25, 25, 0.85);
    border: none;
    color: #fff;
    font-size: 1.5rem;
    padding: 12px 14px;
    cursor: pointer;
    transition: background 0.3s, box-shadow 0.3s;
    border-radius: 6px;
    box-shadow: 0 0 5px transparent;
    z-index: 10;
}

.category-nav-button:hover {
    background: rgba(229, 9, 20, 0.9);
    box-shadow: 0 0 12px rgba(229, 9, 20, 0.8);
}

.categories-list {
    display: flex;
    overflow-x: hidden; /* JavaScriptÎ°ú Ïä§ÌÅ¨Î°§ Ï†úÏñ¥ */
    gap: 13px;
    padding: 10px 0;
    scrollbar-width: none;
    flex: 1;
    margin: 0 5px;
    justify-content: center;
}

.categories-list::-webkit-scrollbar {
    display: none;
}

.category-item {
    background: #98111f;
    padding: 12px 24px;
    border-radius: 100px;
    cursor: pointer;
    white-space: nowrap;
    font-size: 0.9rem;
    font-weight: 700;
    color: #fff;
    box-shadow: 0 0 5px rgba(229, 9, 20, 0.6);
    transition: background 0.3s, transform 0.2s, box-shadow 0.3s;
}

.category-item:hover {
    background: #ff1a1a;
    transform: scale(1.07);
    box-shadow: 0 0 15px rgba(255, 25, 25, 0.8);
}

.category-item.selected {
    background: #e50914;
    box-shadow: 0 0 18px rgba(229, 9, 20, 1);
}

.content-sections{
    margin-top: 0px; /* Ïπ¥ÌÖåÍ≥†Î¶¨ Î∞î Î∞îÎ°ú ÏïÑÎûòÏóê Î∂ôÎèÑÎ°ù */
    padding: 0 10px; /* Featured ContentÏôÄ ÎèôÏùºÌïú Ï¢åÏö∞ Ìå®Îî© */
    background-color: #141414; /* ÎÑ∑ÌîåÎ¶≠Ïä§ Î∞∞Í≤ΩÍ≥º Í∞ôÏùÄ Í≤ÄÏùÄÏÉâÏúºÎ°ú ÏßÅÏ†ë ÏßÄÏ†ï */
}

/* Í∞Å Ï∂îÏ≤ú ÏÑπÏÖò */
.recommendation-section {
    margin-top: 0px; /* ÏÑπÏÖò Í∞Ñ Í∞ÑÍ≤© (10px) */
    margin-bottom: 0px; /* ÏÑπÏÖò ÏïÑÎûò ÎßàÏßÑ Ï†úÍ±∞ */
    background-color: #0a0a0a; /* ÎÑ∑ÌîåÎ¶≠Ïä§ Î∞∞Í≤ΩÍ≥º Í∞ôÏùÄ Í≤ÄÏùÄÏÉâÏúºÎ°ú ÏßÅÏ†ë ÏßÄÏ†ï */
}

.section-title {
    font-size: 1.3rem;
    font-weight: 700;
    margin-bottom: 5px; /* Ï†úÎ™© ÏïÑÎûò Í∞ÑÍ≤© */
    color: #c2bbbb;
    text-shadow: 5px 4px 4px rgba(241, 6, 6, 0.7);
}

/* Ï∂îÏ≤ú Î°úÏö∞ (Row Ïª¥Ìè¨ÎÑåÌä∏ Ïô∏Î∂Ä) */
.recommendation-row {
    margin-bottom: 0px; /* Î°úÏö∞ ÏïÑÎûò Í∞ÑÍ≤© Ï†úÍ±∞ */
    background-color: transparent; /* Î©îÏù∏ Î∞∞Í≤ΩÏÉâÏùÑ ÏÉÅÏÜçÎ∞õÎèÑÎ°ù Ìà¨Î™ÖÌïòÍ≤å ÏÑ§Ï†ï */
}

.row-title {
    font-size: 1.5rem;
    font-weight: 700;
    margin-bottom: 5px; /* Ï†úÎ™© ÏïÑÎûò Í∞ÑÍ≤© */
    color: #fff;
    text-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
}

.loading {
    text-align: center;
    font-size: 1.2rem;
    color: #bbb;
    padding: 50px 0;
}

/* Row Ïª¥Ìè¨ÎÑåÌä∏ ÏûêÏ≤¥Ïùò Ïª®ÌÖåÏù¥ÎÑà */
.row-container {
    position: relative;
    z-index: 1;
    border-radius: 8px;
    border: 2px solid transparent;
    overflow: visible;
    background-color:#0a0a0a; /* Î©îÏù∏ Î∞∞Í≤ΩÏÉâÏùÑ ÏÉÅÏÜçÎ∞õÎèÑÎ°ù Ìà¨Î™ÖÌïòÍ≤å ÏÑ§Ï†ï */
}

.row-container::before {
    content: "";
    position: absolute;
    top: -3px;
    left: -3px;
    right: -3px;
    bottom: -3px;
    border-radius: 11px;
    pointer-events: none;
    z-index: -1;

    border: 0.5px solid transparent;
    background:
            linear-gradient(
                    90deg,
                    transparent 0%,
                    /*!*#ee0808 10%,*! #96968f 20%,*/
                    /*#d2bc2c 30%,*/
                    /*#0eea22 40%,*/
                    /*#06a0f3 50%,*/
                    /*#091be5 60%,*/ #ef0519 30%,
                    transparent 80%
            );
    background-size: 200% 200%;

    -webkit-mask:
            linear-gradient(#a304f6 0 0) content-box,
            linear-gradient(#fff 0 0);
    -webkit-mask-composite: destination-out;
    mask-composite: exclude;

    animation: glowMoveLinear 4s linear infinite;
}

@keyframes glowMoveLinear {
    from {
        background-position: 0% 0%;
    }
    to {
        background-position: 200% 0%;
    }
}

/* Ïã§Ï†ú Í∞ÄÎ°ú Ïä§ÌÅ¨Î°§ Î°úÏö∞ */
.row {
    display: flex;
    overflow-x: scroll; /* Ïä§ÌÅ¨Î°§Î∞îÍ∞Ä Î≥¥Ïù¥ÎèÑÎ°ù hidden -> scroll */
    gap: 10px; /* Î°úÏö∞ ÏïÑÏù¥ÌÖú Í∞ÑÍ≤© Îçî Ï§ÑÏûÑ */
    padding-bottom: 13px;
    padding-top: 13px;
    background-color: #0a0a0a; /* Î©îÏù∏ Î∞∞Í≤ΩÏÉâÏùÑ ÏÉÅÏÜçÎ∞õÎèÑÎ°ù Ìà¨Î™ÖÌïòÍ≤å ÏÑ§Ï†ï */
}

.row::-webkit-scrollbar {
    display: none; /* ÏõπÌÇ∑ Ïä§ÌÅ¨Î°§Î∞î Ïà®ÍπÄ */
}

.row-item {
    flex: 0 0 auto;
    width: 110px;
    height: 160px;
    /* #222 ÎåÄÏã† ÎÑ∑ÌîåÎ¶≠Ïä§ Î∞∞Í≤ΩÏÉâÏù∏ #141414 ÎòêÎäî Îçî ÏßÑÌïú Í≤ÄÏùÄÏÉâÏúºÎ°ú Î≥ÄÍ≤Ω */
    background: #0a0a0a; /* Í∞Å Ìè¨Ïä§ÌÑ∞ Îí§ Î∞∞Í≤ΩÎèÑ ÎÑ∑ÌîåÎ¶≠Ïä§ Í≤ÄÏùÄÏÉâÏúºÎ°ú */
    border-radius: 6px;
    overflow: hidden;
    transition: transform 0.3s, box-shadow 0.3s;
    box-shadow: 0 0 5px rgba(0,0,0,0.7);
}

.row-item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
}

.row-item:hover {
    transform: scale(1.1);
    box-shadow: 0 6px 20px rgba(229, 9, 20, 0.9);
    border: 1px solid #e50914;
}

/* Î°úÏö∞ ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò Î≤ÑÌäº (Ï¢åÏö∞ Ïä§ÌÅ¨Î°§ Î≤ÑÌäº) */
.row-nav-button {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 40px;
    background: rgba(28, 27, 27, 0.6);
    border: none;
    color: #d7caca;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    opacity: 0;
    transition: opacity 0.3s;
    border-radius: 8px;
    z-index: 10;
    box-shadow: 0 0 8px rgba(10, 10, 10, 0.7);
}

.row-container:hover .row-nav-button {
    opacity: 1;
}

.row-nav-button-left {
    left: 0px;
}

.row-nav-button-right {
    right: 0px;
}

.member-mode-filters {
    display: flex;
    justify-content: center; /* ÌïÑÌÑ∞ Í∑∏Î£πÎì§ÏùÑ Ï§ëÏïô Ï†ïÎ†¨ */
    align-items: flex-start; /* ÌïÑÌÑ∞ Í∑∏Î£πÎì§ÏùÑ ÏÉÅÎã® Ï†ïÎ†¨ */
    gap: 30px; /* ÌïÑÌÑ∞ Í∑∏Î£π Í∞ÑÏùò Í∞ÑÍ≤© */
    margin-top: 20px; /* Ï∂îÏ≤ú Ïπ¥ÌÖåÍ≥†Î¶¨ Î∞î ÏïÑÎûò Ïó¨Î∞± */
    margin-bottom: 30px; /* Ï∂îÏ≤ú ÏÑπÏÖò ÏúÑ Ïó¨Î∞± */
    padding: 15px 50px; /* Ï¢åÏö∞ Ìå®Îî© */
    background-color: #1a1a1a; /* ÌïÑÌÑ∞ ÏÑπÏÖòÏùò Î∞∞Í≤ΩÏÉâ (ÎÑ∑ÌîåÎ¶≠Ïä§ Ïπ¥Îìú Î∞∞Í≤ΩÏÉâÍ≥º Ïú†ÏÇ¨) */
    border-radius: 8px; /* Î™®ÏÑúÎ¶¨ Îë•Í∏ÄÍ≤å */
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.5); /* Í∑∏Î¶ºÏûê */
    flex-wrap: wrap; /* ÏûëÏùÄ ÌôîÎ©¥ÏóêÏÑú Ï§ÑÎ∞îÍøà ÌóàÏö© */
}

.filter-group {
    display: flex;
    flex-direction: column;
    gap: 10px; /* Î†àÏù¥Î∏îÍ≥º Î≤ÑÌäº Í∑∏Î£π ÏÇ¨Ïù¥ Í∞ÑÍ≤© */
    align-items: center; /* ÌïÑÌÑ∞ Í∑∏Î£π ÎÇ¥ ÏöîÏÜåÎì§ Ï§ëÏïô Ï†ïÎ†¨ */
    min-width: 150px; /* ÏµúÏÜå ÎÑàÎπÑ */
}

.filter-group label {
    font-size: 1rem;
    color: #f5f5f5;
    font-weight: 600;
    margin-bottom: 5px;
    text-align: center;
}

/* Î≤ÑÌäº Í∑∏Î£π Ïä§ÌÉÄÏùº (recommendation.cssÏóê ÏûàÎäî Í≤ÉÍ≥º Ïú†ÏÇ¨) */
.button-group {
    display: flex;
    gap: 5px; /* Î≤ÑÌäº ÏÇ¨Ïù¥ Í∞ÑÍ≤© */
    flex-wrap: nowrap; /* Î≤ÑÌäº Ï§ÑÎ∞îÍøà Î∞©ÏßÄ */
    justify-content: center; /* Î≤ÑÌäºÎì§ Ï§ëÏïô Ï†ïÎ†¨ */
}

/* genre-item Ïä§ÌÉÄÏùº Ïû¨ÌôúÏö© (MainContentÏóê Ïù¥ÎØ∏ Ï†ïÏùòÎêòÏñ¥ ÏûàÏùÑ Ïàò ÏûàÏùå) */
/* ÎßåÏïΩ Ïù¥Ï†ÑÏóê genre-item Ïä§ÌÉÄÏùºÏù¥ recommendation.cssÏóêÎßå ÏûàÏóàÎã§Î©¥, MainContent.cssÏóêÎèÑ Ï∂îÍ∞ÄÌï¥Ïïº Ìï©ÎãàÎã§. */
/* Ïó¨Í∏∞ÏÑúÎäî recommendation.cssÏóê ÏûàÎäî genre-item Ïä§ÌÉÄÏùºÏùÑ MainContent.cssÏóêÎèÑ Î≥µÏÇ¨ÌñàÎã§Í≥† Í∞ÄÏ†ïÌï©ÎãàÎã§. */
.genre-item {
    padding: 8px 15px;
    border-radius: 4px;
    background: #555;
    color: #fff;
    font-size: 0.9rem;
    cursor: pointer;
    transition: background 0.3s, color 0.3s, transform 0.2s;
    border: 1px solid #666;
    flex: 1;
    text-align: center;
    white-space: nowrap; /* ÌÖçÏä§Ìä∏ Ï§ÑÎ∞îÍøà Î∞©ÏßÄ */
}

.genre-item.selected {
    background: #e50914;
    border-color: #e50914;
    font-weight: 600;
    transform: scale(1.05); /* ÏÑ†ÌÉù Ïãú ÏïΩÍ∞Ñ Ïª§ÏßÄÎèÑÎ°ù */
}

.genre-item:hover {
    background: #777;
    color: #fff;
}